}.v-dialog {
  font-weight: 100;
  -webkit-box-align: center;
          align-items: center;
  bottom: 0;
  display: none;
  -ms-flex-align: center;
  -ms-flex-pack: center;
  -webkit-box-pack: center;
          justify-content: center;
  left: 0;
  opacity: 0;
  overflow: hidden;
  padding: 8px;
  position: fixed;
  right: 0;
  top: 0;
}
.v-dialog.v-dialog-sm .v-dialog-container {
  max-width: 320px;
  padding: 0 8px;
}
.v-dialog.v-dialog-lg .v-dialog-container {
  -webkit-box-shadow: none;
          box-shadow: none;
  max-width: 960px;
}
.v-dialog.active,
.v-dialog:target {
  display: -webkit-box;
  display: flex;
  display: -ms-flexbox;
  opacity: 1;
  z-index: 400;
}
.v-dialog.active .v-dialog-overlay,
.v-dialog:target .v-dialog-overlay {
  background: rgba(248, 249, 250, 0.75);
  bottom: 0;
  cursor: default;
  display: block;
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
}
.v-dialog.active .v-dialog-container,
.v-dialog:target .v-dialog-container {
  -webkit-animation: slide-down 0.2s ease 1;
          animation: slide-down 0.2s ease 1;
  z-index: 1;
}
.v-dialog-container {
  background: #fff;
  border-radius: 2px;
  -webkit-box-shadow: 0 4px 10px rgba(69, 77, 93, 0.3);
          box-shadow: 0 4px 10px rgba(69, 77, 93, 0.3);
  display: -webkit-box;
  display: flex;
  display: -ms-flexbox;
  -ms-flex-direction: column;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
          flex-direction: column;
  max-height: 75vh;
  max-width: 70%;
  padding: 0 16px;
  width: 100%;
  color: #50596c;
}
.v-dialog-container.v-dialog-fullheight {
  max-height: 100vh;
}
.v-dialog-container .v-dialog-header {
  color: #454d5d;
  padding: 16px;
}
.v-dialog-container .v-dialog-header .v-dialog-title {
  font-size: 120%;
  font-weight: 500;
}
.v-dialog-container .v-dialog-body {
  overflow-y: auto;
  padding: 6px;
  position: relative;
}
.v-dialog-container .v-dialog-body .v-dialog-input {
  display: block;
  width: 100%;
}
.v-dialog-container .v-dialog-body .prompt-error-message {
  color: #131010;
  font-size: 14px;
}
.v-dialog-container .v-dialog-body .content textarea {
  height: 75px;
  border: 0px;
  font-size: 13px;
  font-weight: 300;
  resize: none;
  text-align: center;
  margin-bottom: 50px;
}
.v-dialog-container .v-dialog-body .content .lg {
  height: 145px;
}
.v-dialog-container .v-dialog-footer {
  /* padding: 16px; */
  text-align: right;
}
.v-dialog-container .v-dialog-btn {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: transparent;
  border: none;
  color: #5cb85c;
  cursor: pointer;
  display: inline-block;
  font-size: 16px;
  /* height: 36px; */
  line-height: 20px;
  outline: none;
  padding: 7px 8px;
  text-align: center;
  text-decoration: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  vertical-align: middle;
  white-space: nowrap;
  font-weight: 400;
}
.v-dialog-container .v-dialog-btn.success {
  color: #50596c;
}
.v-dialog-container .v-dialog-btn.v-dialog-btn-danger {
  color: #d9534f;
}
.v-dialog-container .v-dialog-btn.v-dialog-btn-close {
  height: 16px;
  line-height: 16px;
  margin-left: 8px;
  margin-right: -15px;
  padding: 0;
  width: 16px;
  float: right;
  color: #50596c;
}
.v-dialog-container .v-dialog-btn.v-dialog-btn-close:before {
  content: "\2715";
}
.v-dialog-container .v-dialog-btn.disabled,
.v-dialog-container .v-dialog-btn:disabled,
.v-dialog-container .v-dialog-btn[disabled] {
  cursor: default;
  opacity: 0.5;
  pointer-events: none;
