/** 추가내용: 모바일 웹 브라우저의 가로뷰에서 글자 크기가 확대되는 현상 방지 */

* {
	-webkit-text-size-adjust: none;
  	-webkit-user-select : none;  
}

input,select,textarea{
    -webkit-user-select : auto;
}

/** 추가내용: 웹 브라우저 기본 사이즈 정의 */

html, body {
	width: 100%;
	overflow-x: hidden;
}

.not-scroll{
    position: fixed;
    overflow: hidden;
    width: 100%;
    height: 100%;
}

*:focus { outline:none; }

/** 아래 코드는 reset.css의 원본 내용 */

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video,button,select,input {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 12px;
	/*font: inherit;*/
	vertical-align: baseline;
	font-family:'Nanum Barun Gothic','Apple SD Gothic Neo','malgun gothic','\B9D1\C740   \ACE0\B515','\B3CB\C6C0','dotum';
	text-decoration:none;
    color: #666;
}

/* HTML5 display-role reset for older browsers */

article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}

ol, ul {
	list-style: none;
}

blockquote, q {
	quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}

table {
	border-collapse: collapse;
	border-spacing: 0;
}

button{cursor: pointer;}

em{
    font-style: normal;
}

